:root {
	--First-color: #00317a;
	--Second-color: #123468;
	--Third-color: #214685;
	--Fourth-color: #dcdcdc;
	--Fifth-color: #81bcba;
	--Sixth-color: #81bcba46;
	--Seventh-color: rgba(8, 60, 119, 0.562);
}

* {
	box-sizing: border-box;
}

.services__container {
	width: 100%;
	margin: auto;
	text-align: center;
	background-color: #81bcba46;
	background-color: var(--Sixth-color);
}

.services__container h1 {
	color: #123468;
	color: var(--Second-color);
	padding: 2rem;
	text-shadow: 2px 2px 2px rgba(8, 60, 119, 0.562);
	text-shadow: 2px 2px 2px var(--Seventh-color);
	font-size: 3rem;
	font-family: 'Sansita Swashed', cursive;
}

.effect7 {
	position: relative;
	box-shadow: -15px 17px 5px rgba(0, 0, 0, 0.5), 0 0 28px rgb(0 0 0) inset;
}

/* Card */
.services__container h3 {
	margin: 0;
	color: #214685;
	color: var(--Third-color);
	font-size: 3rem;
	padding: 2rem;
	margin: 0 12rem;
	text-shadow: 2px 2px 2px rgba(8, 60, 119, 0.562);
	text-shadow: 2px 2px 2px var(--Seventh-color);
	font-family: 'Sansita Swashed', cursive;
}

.containerCard {
	display: flex;
	flex-wrap: wrap;
	padding: 4rem;
	margin: 0 12rem;
	flex-direction: row-reverse;
	justify-content: space-around;
	align-content: center;
	background-color: rgba(8, 60, 119, 0.562);
	background-color: var(--Seventh-color);
}

.card-info {
	flex-basis: 20rem;
	margin: 2rem;
	border: 4px #123468 solid;
	border: 4px var(--Second-color) solid;
	border-radius: 23px 0;
	background-color: #dcdcdc;
	background-color: var(--Fourth-color);
	box-shadow: 4px 4px 4px azure;
	overflow: hidden;
	color: #00317a;
	color: var(--First-color);
	position: relative;
}

.card-info h4 {
	padding: 0.7rem;
	color: #123468;
	color: var(--Second-color);
	font-weight: bold;
	font-family: 'Sansita Swashed', cursive;
}

.card-info p {
	text-align: left;
	padding: 0 1rem;
	margin-bottom: 0.2rem;
	font-weight: bold;
}

.price {
	font-weight: bold;
	color: #81bcba;
	color: var(--Fifth-color);
}

.chalet-img-card {
	height: 35vh;
	width: 100%;
	border-radius: 20px 0px 55% 0;
	transition: all 0.3s ease 0s;
}

.image-card-div {
	height: 35vh;
	width: 100%;
	margin-bottom: 4rem;
}

.image-card-div :hover {
	border-radius: 20px 0;
	transform: scale(1.5);
}

.btnBooking {
	margin: 1rem;
	background-color: #123468;
	background-color: var(--Second-color);
	border: #123468 2px solid;
	border: var(--Second-color) 2px solid;
	color: #dcdcdc;
	color: var(--Fourth-color);
	font-family: 'Sansita Swashed', cursive;
	position: absolute;
	bottom: 0;
}

.btnBooking:hover {
	background-color: #81bcba;
	background-color: var(--Fifth-color);
	transform: scale(0.9);
}

/* slider */
.slider-container {
	display: flex;
	padding: 1rem;
	flex-wrap: wrap;
	justify-content: center;
}

.sliderImg {
	width: 89vw;
	height: 82vh;
}

.space {
	width: 100%;
	height: 26vh;
}

