:root {
	--First-color: #00317a;
	--Second-color: #123468;
	--Third-color: #214685;
	--Fourth-color: #dcdcdc;
	--Fifth-color: #81bcba;
	--Sixth-color: #81bcba46;
	--Seventh-color: #16375bb4;
	--Eighth-color: #f8f9fab4;
}

.book__container {
	width: 100%;
	height: auto;
	margin: auto;
	text-align: center;
	background-image: url(https://img.rawpixel.com/s3fs-private/rawpixel_images/website_content/rm27-tong-62-nature.jpg?w=1000&dpr=1&fit=default&crop=default&q=65&vib=3&con=3&usm=15&bg=F4F4F3&ixlib=js-2.2.1&s=e50fb7e1dd5e1b00d8eeda4c1420420c);
	background-attachment: fixed;
	background-repeat: no-repeat;
	background-size: cover;
	padding: 2rem 0 6rem 0rem;
}

.book__container h1 {
	color: #00317a;
	color: var(--First-color);
	padding: 2rem;
	text-shadow: 2px 2px 2px #16375bb4;
	text-shadow: 2px 2px 2px var(--Seventh-color);
	font-size: 3rem;
	font-family: 'Sansita Swashed', cursive;
}

.book__container h1 .far,
.paymedMethod h3 .far {
	color: #81bcba;
	color: var(--Fifth-color);
	margin: 0 1rem;
	font-size: 3rem;
}

.bookSectoin {
	background-color: #16375bb4;
	background-color: var(--Seventh-color);
	/* height: auto; */
	/* width: auto; */
	color: #dcdcdc;
	color: var(--Fourth-color);
	padding: 3rem;
	margin: 0 13rem;
	display: flex;
	flex-wrap: wrap;
	flex-direction: row;
	justify-content: space-around;
	align-content: center;
}

.imageOne {
	display: flex;
	flex-wrap: wrap;
	width: 23rem;
	flex-direction: column;
	justify-content: space-around;
	align-content: center;
}

.chalet-img-book {
	flex-basis: 20rem;
	width: 20vw;
	height: 20vh;
	margin: 0.7rem 0;
	box-shadow: 4px 2px 8px black;
}

.chalet-info-book {
	display: flex;
	flex-wrap: wrap;
	width: 33rem;
	color: #123468;
	color: var(--Second-color);
	flex-direction: column;
	justify-content: space-around;
	align-content: center;
	box-shadow: 8px 8px 10px #123468;
	box-shadow: 8px 8px 10px var(--Second-color);
	background-color: #dcdcdc;
	background-color: var(--Fourth-color);
}

.DataPi {
	padding: 0.5rem;
	border-radius: 8px;
	width: 12rem;
	margin-left: 8rem;
}

.detailes {
	width: 100%;
	padding: 2rem;
	color: #dcdcdc;
	color: var(--Fourth-color);
	background-color: #214685;
	background-color: var(--Third-color);
}

.detailes h3 {
	padding-bottom: 2rem;
	text-shadow: 2px 2px 2px #123468;
	text-shadow: 2px 2px 2px var(--Second-color);
}

.detailes h3,
.paymedMethod h3 {
	font-family: 'Sansita Swashed', cursive;
}

.detailes .paymedMethod h3 {
	color: #214685;
	color: var(--Third-color);
	text-shadow: 1px 1px 1px #81bcba46;
	text-shadow: 1px 1px 1px var(--Sixth-color);
}

.detailes .paymedMethod h3 .far {
	font-size: 1.5rem;
}

.detailes p {
	text-align: left;
}

.btn-submit {
	background-color: #81bcba;
	background-color: var(--Fifth-color);
	font-weight: bold;
	border: 1px 1px 1px #81bcba;
	border: 1px 1px 1px var(--Fifth-color);
}

.btn-submit :hover {
	background-color: #214685;
	background-color: var(--Third-color);
}

.paymedMethod {
	background-color: #f8f9fab4;
	background-color: var(--Eighth-color);
	padding: 1rem;
}

.paymedMethod h3 .far {
	font-size: 1rem;
}

.radioPaymen {
	font-size: 1em;
	font-weight: bold;
	padding: 1rem;
	color: #123468;
	color: var(--Second-color);

}

.radioPaymen input {
	margin-left: 1rem;
}

.card-img-Icon {
	width: 12vw;
	height: 5vh;
	margin: 0.7rem 0;
}

.card-credit {
	padding: 1rem;
}

.mb-0 {
	color: #81bcba;
	color: var(--Fifth-color);
}

.col-form-label {
	font-weight: bold;
}

/* Popup */
.Msg {
	width: 500px;
	max-width: 100%;
	box-sizing: border-box;
	padding: 15px;
	margin: 10% auto 0;
	text-align: center;
}

.Msg h2 {
	color: #81bcba;
	color: var(--Fifth-color);

}

.Msg h5 {
	color: #123468;
	color: var(--Second-color);
	margin-top: 1rem;
	font-size: 0.9rem;
}

.Button-Close {
	background: #123468;
	background: var(--Second-color);
	background: #81bcba;
	background: var(--Fifth-color);
	height: 50px;
	line-height: 50px;
	width: 50%;
	border: none;
	border-radius: 12px;
	outline: none;
	margin: auto !important;
	cursor: pointer;
	color: #ffffff;
	font-size: 1.2em;
	transition: all 0.3s ease-in-out;
}

@media screen and (max-width: 650px) {
	.Button .formBox {
		width: 90%;
	}
}

.Msg h3 {
	text-align: center;
}

/* End Popup */
@media screen and (max-width: 1490px) {
	.bookSectoin {
		margin: 0 4rem;
	}
}

/* Responsive layout - makes the two columns stack on top of each other instead of next to each other */
@media screen and (max-width: 1136px) {
	.chalet-img-book {
		flex-basis: 16rem;
	}

	.imageOne {
		width: auto;
	}
}

@media screen and (max-width: 908px) {
	.imageOne {
		flex-direction: row;
	}

	.chalet-img-book {
		flex-basis: 2rem;
		margin: 1rem;
		width: 27vw;
		height: 22vh;
	}
}

@media screen and (max-width: 636px) {
	.chalet-img-book {
		width: 36vw;
		height: 20vh;
	}

	.bookSectoin {
		margin: 0 2rem;
	}
}

@media screen and (max-width: 419px) {
	.bookSectoin {
		margin: 0;
	}

	.chalet-img-book {
		width: 66vw;
	}
}

@media screen and (max-width: 280px) {
	.chalet-info-book {
		width: 16rem;
	}

	.DataPi {
		padding: 0.5rem;
		border-radius: 8px;
		width: 11rem;
		margin-left: 0rem;
	}
}
